* {
    box-sizing: border-box;
}

body {
    height: 100%;
    background-color: #e6ebe7;
    margin: 0;
    padding: 0;

}

html, body, .container, #root{
    height: 100%;
    display: flex;
    justify-content: center;
    width: 100%;
    
}

.loader {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 60px;
    font-weight: 700;
}

.movies{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 50px;
    width: 80%;
}

.movies .movies_movie{
    width: 45%;
    height: fit-content;
    background-color: white;
    margin-bottom: 70px;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    position: relative;
    padding: 20px;
    box-shadow: 5px 4px 10px 1px #6d6d6d;
}


.movies_movie img{
    max-width: 200px;
    box-shadow: 5px 4px 11px 2px #323232;
    margin-bottom: 4px;
    margin-top: -40px;
    margin-left: -30px;
    position: relative;
}

.movie_data {
    margin-left: 20px;
    
}

.movie_title, .movie_year, .movie_genres, .movie_rating, .movie_runtime, .movie_mpa_rating {
    margin: 0;
    padding: 1px;
}

.movie_year{
    margin-bottom: 15px;
}

.movie_title{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.movie_genres {
    margin-top: 15px;
    font-size: 20px;
}

.movie_summary{
    text-shadow: 0px 0 #323232,
                0 0px #323232, 
                0px 0 #323232,
                0 0px #323232;
    opacity: 0.6;
}

h5 {
    opacity: 0.6;
}